.slider.owl-carousel .owl-item a:after {
	content: '';
	width: 100%;
	height: calc(100% + 30px);
	position: absolute;
	top: -15px;
	background-color: #fff;
	z-index: -1;
	border-radius: 6px;
	opacity: 0;
	transition: 0.5s all ease;
	-webkit-transition: 0.5s all ease;
	-o-transition: 0.5s all ease;
	-moz-transition: 0.5s all ease;
}

.slider.owl-carousel .owl-item a:before {
	content: '';
	width: 100%;
	border: 1px solid #0af;
	height: calc(100% + 30px);
	position: absolute;
	border-radius: 6px;
	top: -15px;
	left: 0;
	right: 0;
	opacity: 0;
	box-shadow: 0 0 10px 5px rgba(0,0,0,0.05);
	transition: 0.5s all ease;
	-webkit-transition: 0.5s all ease;
	-o-transition: 0.5s all ease;
	-moz-transition: 0.5s all ease;
}

