}

.wrap_task_rating .box_rating .ratingwrap {
	display: flex;
	justify-content: space-between;
}

.wrap_task_rating .box_rating .ratingwrap .rate_icon {
	width: 15%;
	text-align: center;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap {
	width: 70%;
	display: flex;
	justify-content: space-between;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio {
	position: relative;
	width: 20%;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:checked+label:before,
.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:not(:checked)+label:before {
	content: "";
	position: absolute;
	left: 0;
	margin: auto;
	right: 0;
	top: 0;
	width: 40px;
	height: 40px;
	border: solid 1.4px #d1d7e4;
	border-radius: 12px;
	background: transparent;
	z-index: 9;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:checked+label:after,
.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:not(:checked)+label:after {
	content: "";
	width: 40px;
	height: 40px;
	background: #cef;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	margin: auto;
	border-radius: 12px;
	opacity: 0;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:checked+label:after {
	opacity: 1;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:checked+label:before {
	border-color: #0af;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio [type="radio"]:checked+label span {
	opacity: 1;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio label {
	font-size: 14px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: center;
	color: #3a4a66;
	padding-top: 42px;
	display: block;
	letter-spacing: -0.2px;
	cursor: pointer;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio label span {
	opacity: 0;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio label:hover span {
	opacity: 1;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio label:hover:after {
	background: #cef !important;
	opacity: 1 !important;
}

.wrap_task_rating .box_rating .ratingwrap .chekbox_wrap .check_radio label:hover:before {
	border-color: #cef !important;
}

